/*RESUME STYLES*/

  #resume {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 3%;
    color: rgb(122, 122, 122);
    margin-bottom: 4em;
  }

  .resumeDiv {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  .resumeDiv h2 {
    text-transform: uppercase;
    font-size: 3em;
    color: rgb(122, 122, 122);
  }

  .resumeDiv h2:hover {
    color: rgb(156, 172, 173);
  }

  @media only screen and (min-device-width: 320px) and (max-device-height: 568px) and (orientation : portrait) and (-webkit-device-pixel-ratio: 2){
    .resumeDiv h2 {
      text-transform: uppercase;
      font-size: 2.2em;
      margin-bottom: 1em;
    }
  }



