/*CONTACT STYLES*/
html {
  scroll-behavior: smooth;
}

  .contact {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 2em;
  }

  .contact-h2 {
    text-align: center;
    text-transform: uppercase;
    font-size: 3em;
    color: rgb(122, 122, 122);
  }

  .contactDiv {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    margin-top: 3%;
    margin-bottom: 1em;
    color: rgb(122, 122, 122);

  }

  @media only screen and (min-device-width: 320px) and (max-device-height: 568px) and (orientation : portrait) and (-webkit-device-pixel-ratio: 2){
    .contactLinks {
      font-size: 2.2em;
    }

    .contact-h2 {
      text-align: center;
      text-transform: uppercase;
      font-size: 2.2em;
      color: rgb(122, 122, 122);
      margin-bottom: 1em;
    }

  }



